.demo {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 20px;
    border: 1px solid #ccc;
    color: #000;
}

table {
    width: 100%;
}

tr>td:nth-child(3) {
    color: deepskyblue;
}
tr>td>span {
    border-left: 1px solid #ccc;
    padding: 0 10px;
    color: yellowgreen;
}
tr>td>span:nth-child(1){
    border-left: 0; 
}